*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    font-size: calc(16px + 3.5vw);
    text-align: center;
    margin-top: 9vw;
    padding-bottom: 1vw;
}

.profile-holder {
    margin: 5rem;
    padding: 15rem;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: visible;
}

.profile-pic, .profile-heading{
    object-fit: cover;
    position: absolute;
    bottom: auto;
}

.profile-pic {
    height: auto;
    max-height: 30rem;
    margin-left: auto;
    margin-right: auto;
}

.profile-heading {
    margin-top: 25rem;
    margin-bottom: 0;
    bottom: auto;
    font-size: 13cqw;
}